import React, { useState, useEffect, createRef } from "react";
import { motion } from "framer-motion";
import ScrollSpy from "react-scrollspy-navigation";

import Linkss from "./common/Linkss";
import Href from "./common/Href";

function Navbar() {
  const [all, setAll] = useState("0");
  const [top, setTop] = useState("0");
  const [state, setstate] = useState(true);
  const [flipFlop, setflipFlop] = useState(true);
  const [animate, setanimate] = useState({
    x: 0,
    opacity: 1,
    transition: { duration: ".5", transitionTimingFunction: "easeOutBack" },
  });

  const [clicks, setClicks] = useState([
    { id: 0, href: "Home", statues: true },
    { id: 1, href: "About", statues: false },
    { id: 2, href: "Porfolio", statues: false },
    { id: 3, href: "Services", statues: false },
    { id: 4, href: "resources", statues: false },
    { id: 5, href: "Contact", statues: false },
  ]);
  // const [clicks, setClicks] = useState([
  //   { id: 0, href: "one", statues: true },
  //   { id: 1, href: "two", statues: false },
  //   { id: 2, href: "three", statues: false },
  //   { id: 3, href: "four", statues: false },
  //   { id: 4, href: "five", statues: false },
  //   { id: 5, href: "six", statues: false },
  // ]);
  useEffect(() => {
    if (window.innerWidth < 1201) {
      setanimate({ x: "0", opacity: 0 });
    } else {
      setanimate({
        x: 0,
        opacity: 1,
        transition: { duration: ".5", transitionTimingFunction: "easeOutBack" },
      });
    }
  }, []);
  setTimeout(() => {
    setAll(document.querySelector("#All").offsetHeight);

    // [all] 5000        100 [%]
    // [top] 2500        x  [prog]
  }, 500);
  function handleSlide() {
    setstate(!state);
    const silde = document.getElementById("Slide");

    if (state) {
      console.log(state, " true");
      silde.style.height = "100vh";
      silde.style.borderBottomRightRadius = "0";
      silde.style.borderBottomLeftRadius = "0";
    } else {
      console.log(state, " false");
      silde.style.height = "0";
      silde.style.borderBottomRightRadius = "50%";
      silde.style.borderBottomLeftRadius = "50%";
    }
  }
  function menu() {
    const bur1 = document.querySelector(".bur1");
    const bur2 = document.querySelector(".bur2");
    const bur3 = document.querySelector(".bur3");

    if (flipFlop) {
      bur1.style.transform = "translate( -50% , -50% ) rotate(45deg)";
      bur2.style.opacity = "0";
      bur3.style.transform = "translate( -50% , -50% ) rotate(-45deg)";
      setflipFlop(false);
    } else {
      bur1.style.transform = "translate( -50% , -12px )  rotate(0deg)";
      bur2.style.transform = "translate( -50% , -50% )  rotate(0deg)";
      bur2.style.opacity = "1";
      bur3.style.transform = "translate( -50% , 10px ) rotate(0deg)";
      setflipFlop(true);
    }
  }

  function clickHandler(id) {
    handleSlide();
    menu();
    let clone = clicks;
    clone.map((item) => {
      item.statues = false;
    });
    clone.map((item) => {
      if (item.id === id) {
        item.statues = true;
      }
    });

    setClicks(clone);
  }

  setTimeout(() => {
    const prog = document.getElementById("Progress");
    prog.style.width = "16.6%";
  }, 200);

  let screen = window.innerWidth;
  const [navAnim, setnavAnim] = useState({});
  useEffect(() => {
    console.log("screen ", screen);
    if (window.innerWidth > 1200) {
      console.log(">");
      setnavAnim({
        initial: {
          x: "200px",
          opacity: 0,
        },
        animate: {
          x: 0,
          opacity: 1,
          transition: {
            duration: ".5",
            transitionTimingFunction: "easeOutBack",
          },
        },
      });
      console.log(navAnim);
    } else {
      console.log("less");
      setnavAnim({
        initial: {
          y: "200px",
          opacity: 0,
        },
        animate: {
          y: 0,
          opacity: 1,
          transition: {
            duration: ".5",
            transitionTimingFunction: "easeOutBack",
          },
        },
      });
      setTimeout(() => {
        console.log(navAnim);
      }, 500);
    }
  }, [window.innerWidth]);

  // let navAnim = {
  //   initial: {
  //     x: "200px",
  //     opacity: 0,
  //   },
  //   animate: {
  //     x: 0,
  //     opacity: 1,
  //     transition: { duration: ".5", transitionTimingFunction: "easeOutBack" },
  //   },
  // };
  return (
    <motion.div
      className="sub-nav"
      variants={navAnim}
      initial="initial"
      animate="animate"
    >
      <div className="progress" id="Progress"></div>
      <div className="navbar">
        <div className="logo">
          <div className="in">Sipua</div>
        </div>
        <div className="slide" id="Slide">
          <div className="tabs">
            <ul>
              {clicks.map((item) => (
                <li>
                  <Href
                    key={item.id}
                    id={item.id}
                    href={item.href}
                    clicked={clickHandler}
                    statues={item.statues}
                    ref={createRef()}
                  />
                </li>
              ))}
            </ul>
          </div>
          <div className="links">
            <Linkss />
          </div>
        </div>
        <div onClick={handleSlide} className="more">
          <div className="in">
            <button onClick={menu} className="navbar-toggler">
              <div className="bur1 bur1d bur"></div>
              <div className="bur2 bur2d bur"></div>
              <div className="bur3 bur3d bur"></div>
            </button>
          </div>
        </div>
      </div>
    </motion.div>
  );
}

export default Navbar;

