import React from 'react'


function WhatIPara({one,two,three}) {
    return (
        <div className="what-i-para">
            <div className="para-one">{one}</div>
            <div className="para-two">{two}</div>
            <div className="para-three">{three}</div>
        </div>
    )
}

export default WhatIPara
