import React from "react";
import { motion } from "framer-motion";

function Circle({ title, img }) {
  return (
    <motion.div
      className="circle"
      whileHover={{
        height: "160px",
        transition: { type: "tween" },
      }}
    >
      <motion.img
        src={img}
        alt=""
        whileHover={{ opacity: 1, transition: { ease: "linear" } }}
      />
      <p>{title}</p>
    </motion.div>
  );
}

export default Circle;
